import { prefixUrl } from '@/utils/env'
import Vue from 'vue'
import Router from 'vue-router'
import zixunGuide from '@/views/zixunGuide'
import zixunActivityIndex from '@/views/zixunActivityIndex'
import zixunActivityInfo from '@/views/zixunActivityInfo'
import zixunSelectTicket from '@/views/zixunSelectTicket'
import zixunBuyTicket from '@/views/zixunBuyTicket'
import zixunUserHome from '@/views/zixunUserHome'
import zixunUserEdit from '@/views/zixunUserEdit'
import zixunAbout from '@/views/zixunAbout'
import zixunTicketInfo from '@/views/zixunTicketInfo'
import videoList from '@/views/videoList'

// 
import carConfirm from '@/views/car/carConfirm' // 
import carOrders from '@/views/car/carOrders' // 
import carInsuranceDetail from '@/views/car/carInsuranceDetail' // 
import carInsuranceList from '@/views/car/carInsuranceList' // 
import carInsuranceAgreement from '@/views/car/carInsuranceAgreement' // 
import carBuyInsurance from '@/views/car/carBuyInsurance' // 
import carMyInfo from '@/views/car/carMyInfo' // 
import carMyInsuranceDetail from '@/views/car/carMyInsuranceDetail' // -
import carMyInsuranceList from '@/views/car/carMyInsuranceList' // -
import carMyTransportList from '@/views/car/carMyTransportList' // 
import carRechargeRecord from '@/views/car/carRechargeRecord' // 
import carRecharge from '@/views/car/carRecharge' // 
import carGuide from '@/views/car/carGuide' // 
import carSuccess from '@/views/car/carSuccess' // 

Vue.use(Router)
export default new Router({
  mode: 'history',
  scrollBehavior (to, from, savedPosition) {
    // return { x: 0, y: 450 }
    // return 
  },
  routes: [
    {
      name: '',
      path: prefixUrl + '/zixunGuide',
      component: zixunGuide
    },
    {
      name: '',
      path: prefixUrl + '/zixunActivityIndex',
      component: zixunActivityIndex
    },
    {
      name: '', // 
      path: prefixUrl + '/zixunActivityInfo',
      component: zixunActivityInfo
    },
    {
      name: '', // 
      path: prefixUrl + '/zixunSelectTicket',
      component: zixunSelectTicket
    },
    {
      name: '', // 
      path: prefixUrl + '/zixunBuyTicket',
      component: zixunBuyTicket
    },
    {
      name: '', // 
      path: prefixUrl + '/zixunTicketInfo',
      component: zixunTicketInfo
    },
    {
      name: '', // 
      path: prefixUrl + '/zixunUserhome',
      component: zixunUserHome
    },
    {
      name: '', // 
      path: prefixUrl + '/zixunUserEdit',
      component: zixunUserEdit
    },
    {
      name: '', // 
      path: prefixUrl + '/zixunAbout',
      component: zixunAbout
    },
    {
      name: '', // 
      path: prefixUrl + '/zixunVideo',
      component: videoList
    },
    {
      name: '', // 
      path: prefixUrl + '/carConfirm',
      component: carConfirm
    },
    {
      name: '', // 
      path: prefixUrl + '/carOrders',
      component: carOrders
    },
    {
      name: '', // 
      path: prefixUrl + '/carInsuranceDetail',
      component: carInsuranceDetail
    },
    {
      name: '', // 
      path: prefixUrl + '/carInsuranceList',
      component: carInsuranceList
    },
    {
      name: '', // 
      path: prefixUrl + '/carInsuranceAgreement',
      component: carInsuranceAgreement
    },
    {
      name: '', // 
      path: prefixUrl + '/carBuyInsurance',
      component: carBuyInsurance
    },
    {
      name: '', // 
      path: prefixUrl + '/carMyInfo',
      component: carMyInfo
    },
    {
      name: '-', // 
      path: prefixUrl + '/carMyInsuranceDetail',
      component: carMyInsuranceDetail
    },
    {
      name: '-', // 
      path: prefixUrl + '/carMyInsuranceList',
      component: carMyInsuranceList
    },
    {
      name: '', // 
      path: prefixUrl + '/carMyTransportList',
      component: carMyTransportList
    },
    {
      name: '', // 
      path: prefixUrl + '/carRechargeRecord',
      component: carRechargeRecord
    },
    {
      name: '', // 
      path: prefixUrl + '/carRecharge',
      component: carRecharge
    },
    {
      name: '', // 
      path: prefixUrl + '/carGuide',
      component: carGuide
    },
    {
      name: '', // 
      path: prefixUrl + '/carSuccess',
      component: carSuccess
    }
  ]
})



